<template>
  <div class="the-sidebar d-flex flex-column fill-height flex-start">
    <base-loader :active="loading" />
    <p>  ,           .</p>

    <div class="the-sidebar__row">
      <v-select
        v-model="scope"
        :items="scopes"
        return-object
        item-text="scopeName"
        label=""
        outlined
        hide-details
        clearable
        :loading="industryLoading"
      />
    </div>
    <div class="the-sidebar__row">
      <v-select
        v-model="action"
        :disabled="!scope"
        :items="actionItems"
        label=" "
        :loading="industryLoading"
        return-object
        item-text="name"
        item-value="id"
        outlined
        hide-details
        clearable
      />
    </div>

    <v-btn v-if="isCustomFiltersOpen" class="mb-2" outlined color="info" @click="isCustomFiltersOpen = false">
       
    </v-btn>

    <v-btn v-else outlined color="info" @click="isCustomFiltersOpen = true"> </v-btn>

    <template v-if="isCustomFiltersOpen">
      <p>   ,   </p>

      <div class="the-sidebar__row">
        <div class="text-h6"></div>
        <v-slider
          v-model="customFilters.passability"
          :tick-labels="tickLabels.passability"
          :max="5"
          :min="1"
          :step="1"
          ticks="always"
          tick-size="4"
        />
      </div>
      <div class="the-sidebar__row">
        <div class="text-h6"> ()</div>
        <v-slider
          v-model="customFilters.population"
          :tick-labels="tickLabels.population"
          :max="5"
          :min="1"
          :step="1"
          ticks="always"
          tick-size="4"
        />
      </div>
      <div class="the-sidebar__row">
        <div class="text-h6"></div>
        <v-slider
          v-model="customFilters.competitors"
          :tick-labels="tickLabels.competitors"
          :max="5"
          :min="1"
          :step="1"
          ticks="always"
          tick-size="4"
        />
      </div>
      <div class="the-sidebar__row">
        <div class="text-h6"></div>
        <v-slider
          v-model="customFilters.solvency"
          :tick-labels="tickLabels.solvency"
          :max="5"
          :min="1"
          :step="1"
          ticks="always"
          tick-size="4"
        />
      </div>
      <div class="the-sidebar__row">
        <div class="text-h6">
           
          <base-tooltip>       </base-tooltip>
        </div>
        <v-slider
          v-model="customFilters.availability"
          :tick-labels="tickLabels.availability"
          :max="5"
          :min="1"
          :step="1"
          ticks="always"
          tick-size="4"
        />
      </div>
    </template>

    <v-spacer />

    <v-btn color="primary" :disabled="isSubmitDisabled" @click="submit"> </v-btn>
  </div>
</template>

<script>
import BaseLoader from '@/components/BaseLoader.vue'
import BaseTooltip from '@/components/BaseTooltip.vue'
import backendService from '@/services/backend'

// const PRICE_OPTIONS = [
//   { text: '', value: 'econom' },
//   { text: '', value: 'mass' },
//   { text: '', value: 'premium' }
// ]
// const DEFAULT_SCOPES = [
//   { scopeId: 1, scopeName: ' ', id: 1, name: '' },
//   { scopeId: 1, scopeName: ' ', id: 2, name: ' ' },
//   { scopeId: 2, scopeName: '  ', id: 3, name: '' },
//   { scopeId: 2, scopeName: '  ', id: 4, name: ' ' },
//   { scopeId: 2, scopeName: '  ', id: 5, name: '' },
//   { scopeId: 3, scopeName: ' ', id: 6, name: '' },
//   { scopeId: 3, scopeName: ' ', id: 7, name: '' }
// ]

export default {
  name: 'TheSidebar',
  components: { BaseLoader, BaseTooltip },
  props: {
    loading: { type: Boolean, default: false }
  },
  data() {
    return {
      industryItems: [],
      industryLoading: false,
      scope: null,
      action: null,
      // price: null,

      isCustomFiltersOpen: false,
      customFilters: {
        passability: 0,
        population: 0,
        competitors: 0,
        solvency: 0,
        availability: 0
      },
      tickLabels: {
        passability: ['', '', '', '', ''],
        population: ['<100', '', '~500', '', '>1000'],
        competitors: ['', '', '', '', ''],
        solvency: ['', '', '', '', ''],
        availability: ['>300 ', '', '~750 ', '', '>2 ']
      }
    }
  },
  computed: {
    scopes() {
      return this.industryItems.reduce((acc, value) => {
        if (!acc.find(v => v.scopeId === value.scopeId)) {
          acc.push({ scopeId: value.scopeId, scopeName: value.scopeName })
        }
        return acc
      }, [])
    },
    actionItems() {
      return this.scope
        ? this.industryItems
            .filter(v => v.scopeId === this.scope.scopeId)
            .map(({ name, id, config }) => ({ name, id, config }))
        : []
    },
    // priceOptions() {
    //   return PRICE_OPTIONS
    // },
    isSubmitDisabled() {
      return this.isCustomFiltersOpen ? false : !this.action
    }
  },
  watch: {
    action(val) {
      const config = val && val.config ? val.config : {}

      this.customFilters = {
        passability: config.passability,
        population: config.population,
        competitors: config.competitors,
        solvency: config.solvency,
        availability: config.availability
      }
    }
  },
  created() {
    // this.industryItems = DEFAULT_SCOPES
    this.industryLoading = true
    backendService
      .fetchActivities()
      .then(res => {
        this.industryItems = res
      })
      .finally(() => {
        this.industryLoading = false
      })
  },
  methods: {
    submit() {
      // if (this.isCustomFiltersOpen) {
      //   const { customFilters } = this
      //   this.$emit('submit', { ...customFilters })
      // } else {
      const { action } = this
      this.$emit('submit', { action: action.id })
      // }
    }
  }
}
</script>

<style lang="scss">
.the-sidebar {
  display: flex;
  flex-direction: column;
  max-height: calc(100vh - 90px);
  overflow: auto;
  &__row {
    margin-bottom: 12px;
  }
}
</style>
